<template>
  <div class="erg-extra-drawing">
    <base-erg ref="erg" elName="canvas-extra-drawing" :useThumbnail="true" :useRightMenu="true" :useAttrFocus="true" :editable="'edit'"/>
  </div>
</template>

<script lang="ts" setup>
import { ref, onMounted, computed } from 'vue'
import BaseErg from './Base.vue'
import { GraphEvent } from '@ej/erg-renderer/src'

const erg = ref()

const graph = computed(() => erg.value.graph)
const useExtraDrawing = () => {
  graph.value.on(GraphEvent.BeforeUpdate, (ctx) => {
    graph.value.dataHandler.originData.nodeDataArray.map(({id}, idx) => {
      if (idx % 2) return

      const node = graph.value.dataHandler.originDataMap.get(id)
      if (!node) return
      ctx.save()
      // 
      ctx.font = '16px'
      // 
      ctx.fillStyle = '#F59A23'
      ctx.strokeStyle = '#F59A23'
      // 
      ctx.textAlign = 'right'
      // 
      ctx.textBaseline = 'top'
      ctx.fillText('', node.x + node.width, node.y - 22)
      ctx.lineWidth = 2
      ctx.strokeRect(node.x, node.y, node.width, node.height)
      ctx.restore()
    })
  })
}

onMounted(useExtraDrawing)
</script>

<style lang="postcss">
.erg-extra-drawing {
  height: 100%;
  display: flex;
  flex-direction: column;
}
</style>
